import { Injectable } from '@angular/core';
import { MimeViewerIntl } from './viewer-intl';
import { HelpIntlLt } from './help-intl.lt';

@Injectable()
export class MimeViewerIntlLt extends MimeViewerIntl {
  help = new HelpIntlLt();
  closeLabel = 'Udaryti';
  attributionLabel = 'Teisi priskyrimas';
  attributonCloseAriaLabel = 'Udaryti teisi priskyrimo lang';
  contentsLabel = 'Informacija apie objekt';
  twoPageViewLabel = 'Atvaizduoti po du puslapius';
  singlePageViewLabel = 'Atvaizduoti po vien puslap';
  metadataLabel = 'Metaduomenys';
  licenseLabel = 'Licencija';
  tocLabel = 'Turinys';
  fullScreenLabel = 'Pilno ekrano reimas';
  exitFullScreenLabel = 'Ieiti i pilno ekrano reimo';
  zoomInLabel = 'Priartinti';
  zoomOutLabel = 'Atitolinti';
  previousPageLabel = 'Buvs puslapis';
  nextPageLabel = 'Kitas puslapis';
  homeLabel = 'Grti  pradi';
  rotateCwLabel = 'Pasukti 90';
  searchLabel = 'Paieka';
  clearSearchLabel = 'Ivalyti';
  previousHitLabel = 'Buvs rezultatas';
  nextHitLabel = 'Kitas rezultatas';
  goToPageLabel = 'Persikelti  puslap';
  currentPageLabel = 'Dabartinis puslapis';
  enterPageNumber = 'veskite puslapio numer';
  dropDisabled = 'Atleiskite, bet veiksmas negalimas';

  // ERRORS
  somethingHasGoneWrongLabel = 'Objekto atvaizduoti nepavyko...';
  manifestUriMissingLabel = 'Nerastas objekt srao identifikatorius (ManifestUri)';
  manifestNotValidLabel = 'Netinkamas objekt sraas (Manifest)';
  pageDoesNotExists = 'Nepavyko rasti io paslapio';

  noResultsFoundLabel = (q: string) => {
    return `Objekte nerasta atitikmen <em class="current-search">${q}</em>`;
  };

  resultsFoundLabel = (numberOfHits: number, q: string) => {
    return `${numberOfHits} rezultata${numberOfHits === 1 ? 's' : 'i'} su <em class="current-search">${q}</em>`;
  };
  currentHitLabel = (currentHit: number, numberOfHits: number) => {
    return `${currentHit} i ${numberOfHits} atitikmen`;
  };
}
